* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: var(--primary-color);
}
:root {
  --primary-color: #fafafa;
  --secondary-color: #1e1823;
  --accent-color: #fd9222;
}
a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

.header {
  justify-content: center;
  padding: 20px;
  display: flex;
  gap: 101px;
}

.logo-wrapper {
  display: flex;
  align-items: center;
  gap: 5px;
}
.logo {
  width: 10.98px;
  height: 14.8px;
}
.title {
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
}
.sidebar {
  width: 34vh;
  height: 100vh;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  position: fixed;
  top: 0px;
  right: -520px;
  transition: right 0.5s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 15;
}
.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sidebar-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.sidebar a {
  transition: color 0.3s ease;
}

.sidebar a:hover {
  color: var(--accent-color);
}
.close-icon {
  display: none;
  border: none;
  width: 28px;
  height: 28px;
  background-color: transparent;
  cursor: pointer;
  position: fixed;
  top: 15px;
  transition: right 0.3s ease;
  transition: transform 0.3s ease;
  right: 15px;
  border-radius: 5px;
}
.close-icon:hover {
  transform: scale(1.1) rotate(90deg);
}
.social-icons {
  display: none;
  gap: 16px;
  bottom: 20px;
  right: 108px;
  position: fixed;
}

.social-icons img {
  width: 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
  height: 20px;
  border-radius: 5px;
  padding: 1px;
}
.social-icons img:hover {
  background-color: var(--accent-color);
}
.toggle-button {
  border: none;
  cursor: pointer;
  width: 21px;
  height: 18px;
  background-color: transparent;
  transition: transform 0.3s ease;
}
.toggle-button:hover {
  transform: scale(1.1);
}
.hero {
  display: flex;
  position: relative;
  justify-content: center;
}
.header-img {
  width: 335px;
  height: 622px;
  overflow: hidden;
  padding-top: 85px;
  position: relative;
}

.header-img img {
  border-radius: 107px;
  width: auto;
  max-width: none;
  height: 69%;
  padding: 70px;
  position: absolute;
  top: 44%;
  left: 49%;
  transform: translate(-50%, -50%) rotate(90deg);
}
.hero-content {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 32px;
  z-index: 1;
  bottom: 224px;
  align-items: center;
  width: 100%;
  max-width: 300px;
}
.hero-pharagraph {
  max-width: 300px;
  color: var(--primary-color);
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  text-align: center;
  padding-left: 18px;
  text-align: left;
  padding: 25px;
}
.hero-btn {
  display: flex;
  flex-direction: row;
  gap: 14px;
  justify-content: center;
}

.buy-now-btn {
  width: 110px;
  height: 41px;
  border-radius: 50px;
  color: var(--primary-color);
  background-color: var(--accent-color);
  border: none;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  cursor: pointer;
}
.how-its-made-btn {
  width: 149px;
  height: 41px;
  border-radius: 50px;
  color: var(--primary-color);
  background-color: transparent;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  border: 1px solid var(--primary-color);
  cursor: pointer;
}
.scroll-down-container {
  display: flex;
  gap: 12px;
  align-items: center;
  position: absolute;
  bottom: -118px;
  left: 30px;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  color: var(--primary-color);
}
.scroll-down-btn {
  width: 28px;
  height: 28px;
  border-radius: 14px;
  border: none;
  cursor: pointer;
}
.buy-now-modal {
  display: none;
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(54, 54, 54, 0.8);
  backdrop-filter: blur(5px);
  overflow: hidden;
}
.buy-now-modal-content {
  background-color: var(--primary-color);
  margin: 20 auto;
  padding: 10px;
  border: 1px solid #ededed;
  width: 90vw;
  max-width: 400px;
  height: auto;
  max-height: 95vh;
  overflow-y: auto;
  border-radius: 15px;
  text-align: center;
  position: relative;
}
.buy-now-modal-content::-webkit-scrollbar {
  width: 5px;
}
.buy-now-modal-content::-webkit-scrollbar-thumb {
  background: #999;
  border-radius: 10px;
}
.buy-now-modal-content::-webkit-scrollbar-track {
  margin-top: 10px;
  margin-bottom: 10px;
}

.buy-now-modal-content span {
  font-size: 20px;
}
.buy {
  color: var(--accent-color);
}
.close-modal {
  position: absolute;
  border: none;
  background-color: transparent;
  top: 10px;
  right: 15px;
  font-size: 24px;
  cursor: pointer;
}

.products-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: none;
  background-color: transparent;
  margin-top: 15px;
  justify-content: center;
  align-items: center;
}
.modal-img {
  background-color: #fff;
  border-radius: 20px;
  display: flex;
  align-items: center;
  gap: 110px;
  border: 1px solid #ededed;
}
.modal-img span {
  background-color: var(--primary-color);
  font-size: 14px;
  width: 70px;
  height: 29px;
  padding: 5px;
  border-radius: 100px;
  margin-right: 10px;
  border: 1px solid #ededed;
}
.modal-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  width: 100%;
  gap: 18px;
}

.modal-form label {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #333;
}

.modal-form input {
  width: 263px;
  height: 45px;
  border-radius: 8px;
  border: 1px solid #eeeeee;
  padding-left: 18px;
  font-size: 14px;
  display: block;
}
.modal-form textarea {
  width: 263px;
  border-radius: 8px;
  border: 1px solid #eeeeee;
  padding-left: 18px;
  font-size: 14px;
  display: block;
}
.modal-form input::placeholder,
textarea::placeholder {
  color: #aaa;
}

.modal-form button {
  width: 109px;
  height: 41px;
  color: var(--primary-color);
  background-color: var(--accent-color);
  border-radius: 50px;
  border: none;
  cursor: pointer;
}
.input-wrapper {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  gap: 2px;
}
